/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px 0 28px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;

            span.edgtf-cart-number{
                position: absolute;
                top: -4px;
                right: -8px;
                width: 15px;
                height: 15px;
                line-height: 18px;
                text-align: center;
                border-radius: 50%;
                background-color: $first-main-color;
                font-size: 11px;
                font-family: $additional-heading-font;
                font-weight: 600;
                color: #fff;
            }
        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-cart-icon {
                font-size: 23px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *,
                path,
                line {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            transform: translateY(0);
            opacity: 1;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    transform: translateY(50px);
    left: auto;
    right: 0;
    width: 330px;
    background-color: #000;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    transition: all 0.35s cubic-bezier(.53,.01,.18,1);

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 20px 0;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;
            color: #fff;

            &:hover{
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: block;
            font-size: 15px;
            margin: 0;
            color: #fff;
        }

        .amount {
            display: block;
            margin: 0;
            color: #fff;
        }
    }

    .edgtf-cart-bottom {
        padding: 6px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #fff;
                line-height: 20px;
            }

            .edgtf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            width: 100%;
            height: 64px;
            line-height: 64px;
            padding: 0 30px;
            font-family: $additional-heading-font;
            font-size: 15px;
            letter-spacing: 0.1em;
            font-weight: 700;
            text-transform: uppercase;
            color: #000;
            background-color: #fff;
            text-align: center;
            box-sizing: border-box;
            transition: color .2s ease-out, background-color .2s ease-out;

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: #696969;
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */