/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .edgtf-onsale,
    .edgtf-out-of-stock,
    .edgtf-new-product {
        @include edgtfWooCommerceMarksGeneralStyle();
    }
    
    .edgtf-onsale {
        @include edgtfWooCommerceSaleStyle();
    }
    
    .edgtf-out-of-stock {
        @include edgtfWooCommerceOutOfStockStyle();
    }

    .edgtf-new-product {
        @include edgtfWooCommerceNewProductStyle();
    }
}

.edgtf-woo-single-page {

    &.edgtf-woo-single-thumb-on-right-side {
        .edgtf-single-product-content {

            .edgtf-onsale,
            .edgtf-out-of-stock,
            .edgtf-new-product{
                left: 0;
                right: auto;
            }
        }
    }
    
    .edgtf-single-product-content {
        
        .edgtf-onsale,
        .edgtf-out-of-stock,
        .edgtf-new-product{
            left: 10px;
            right: auto;
        }
    }

}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */