/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonSize();
        @include edgtfButtonSolidColor();

        cursor: pointer;
        z-index: 3;

        &:hover {
            animation: edgtfBtnHover 1.2s;
        }

        &.product_type_external {
            max-width: 90%;
            white-space: normal;
        }

        &.single_add_to_cart_button {
            @include ipad-landscape() {
                vertical-align: top;
            }
        }
    }

    .woocommerce-form-coupon,
    .woocommerce-MyAccount-content,
    .woocommerce-form-login {
        button[type="submit"]:not(.edgtf-search-submit) {
            margin: 35px 0 0 0;
        }
    }

    //.woocommerce-MyAccount-content {
    //    .woocommerce-message,
    //    .woocommerce-Message {
    //    }
    //}
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */