/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 111px;
    $inner-padding: 21px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include edgtfH5();
    }

    .woocommerce-Price-amount{
        font-size: 15px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            display: none;
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 29px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #ebebeb;
                height: 5px;
                position: relative;

                .ui-slider-handle {
                    background-color: $default-text-color;
                    border-radius: 0;
                    cursor: pointer;
                    height: 13px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -4px;
                    width: 5px;
                    z-index: 2;

                    &:last-child {
                        transform: translateX(-50%);
                    }
                }

                .ui-slider-range {
                    background-color: #000;
                    display: block;
                    height: 5px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 25px 0 0;

            .button {
                float: left;
                padding: 0;
                font-size: 15px;
                font-family: $additional-heading-font;
                color: #000;
                border: none;
                background-color: transparent;

                &:hover {
                    animation: none;
                    color: $first-main-color;
                    border: none;
                    background-color: transparent;
                }
            }

            .price_label {
                float: right;
                font-family: $default-text-font;
                font-size: 15px;
                line-height: 30px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;
                padding: 10px 0;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        max-height: $thumb;
                        position: absolute;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 14px 0 8px #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include edgtfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include edgtfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include edgtfButtonDefaultStyle();
                @include edgtfButtonSize();
                @include edgtfButtonSolidColor();

                &:hover {
                    @include edgtfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */