/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 20px;
        margin: 0 0 30px;
        line-height: 64px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        @include ipad-portrait{
            line-height: 27px;
        }

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }

    &.woocommerce-checkout{
        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            line-height: 34px;
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */