/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 35px;
    margin: 0;
    padding: 0 35px;
    color: #fff;
    font-size: 11px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0.16em;
    font-family: $additional-heading-font;
    text-align: center;
    text-transform: uppercase;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin edgtfWooCommerceSaleStyle() {
    right: 0;
    background-color: $first-main-color;
}

@mixin edgtfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: #000;
}

@mixin edgtfWooCommerceNewProductStyle() {
    right: 0;
    background-color: $third-background-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 23px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin edgtfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f4b2\f4b2\f4b2\f4b2\f4b2";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin edgtfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f4b2";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        position: absolute;
        right: 0;
        bottom: 2px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */